(function () {

  angular
    .module('educatinforall-angular-app')
    .service('authentication', authentication);

  authentication.$inject = ['$http', '$window'];

  // create the authentication function that returns functions
  function authentication ($http, $window) {

    // save the token sent by the server to the browser
    var saveToken = function (token) {
      $window.localStorage['mean-token'] = token;
    };

    // get the token saved in the localStorage
    var getToken = function () {
      return $window.localStorage['mean-token'];
    };

    var isLoggedIn = function() {
      var token = getToken();
      var payload;

      if(token){
        payload = token.split('.')[1];
        payload = $window.atob(payload);
        payload = JSON.parse(payload);

        return payload.exp > Date.now() / 1000;
      } else {
        return false;
      }
    };

    var currentUser = function() {
      if(isLoggedIn()){
        var token = getToken();
        var payload = token.split('.')[1];
        payload = $window.atob(payload);
        payload = JSON.parse(payload);
        return {
          email : payload.email,
          name : payload.name
        };
      }
    };

    register = function(user) {
      return $http.post('/api/register', user).then(function(data){
        saveToken(data.data.token);
      });
    };

    // being called in the onSubmit() event handler
    // send the post request to the server and get the response
    login = function(user) {
      return $http.post('/api/login', user).then(function(data) {
        // console.log(data);
        // console.log(data.data);
        // console.log(data.data.token);
        // console.log(data.token);
        saveToken(data.data.token);
      });
    };

    // remove the token in browser localStorage when users logout
    logout = function() {
      $window.localStorage.removeItem('mean-token');
      return true;
    };

    return {
      currentUser : currentUser,
      saveToken : saveToken,
      getToken : getToken,
      isLoggedIn : isLoggedIn,
      register : register,
      login : login,
      logout : logout
    };
  }


})();


(function() {

  angular
    .module('educatinforall-angular-app')
    .service('meanData', meanData);

  meanData.$inject = ['$http', 'authentication'];
  function meanData ($http, authentication) {

    var getProfile = function () {
      return $http.get('/api/profile', {
        headers: {
          Authorization: 'Bearer '+ authentication.getToken()
        }
      });
    };

    var submitManuscript = function(formData, files){
      var fd = new FormData();
      console.log(formData);
      console.log(files);

      angular.forEach(files,function(file){
        fd.append('file', file);
      });
      // fd.append('file', files);
      fd.append('userId', formData.userId);
      fd.append('userEmail', formData.userEmail);
      fd.append('submissionType', formData.submissionType);
      fd.append('submissionCategory', formData.submissionCategory);
      fd.append('submissionFor', formData.submissionFor);
      fd.append('title', formData.title);
      fd.append('abstract', formData.abstract);
      fd.append('keywords', formData.keywords.split(','));
      fd.append('authors', formData.authors.split(','));

      return $http.post('/api/profile/submission', fd, {
        transformRequest: angular.identity,
        headers: {
              'Content-Type': undefined
        }
      });
    }

    return {
      getProfile : getProfile,
      submitManuscript: submitManuscript
    };
  }

})();

